import React from "react";
import "../HomeView/HomeView.scss";
import MainContainer from "../../components/MainContainer/MainContainer";
import Header from "../../components/Header/Header";
import Sidebar from "../../components/Sidebar/Sidebar";
import rates from "../../utils/rates";
import axios from 'axios';
import { connect } from 'react-redux';
import { getHotels } from '../../store/actions/hotels-action';

class HomeView extends React.Component {
    state = {
        hotels: [],
        recommendedHotels: [],
        sort: true,
        currency: 'USD',
        dataFromApi: [],
        symbol: '$'
    };

    convertPrice = value => {
        let x;
        switch (value) {
            case 'USD': x = '$'; break;
            case 'EUR': x = '€'; break;
            case 'PLN': x = 'zl'; break;
            case 'CHF': x = '₣'; break;

            default: x = '$'
        }

        this.setState({
            currency: value,
            symbol: x
        })
    }

    // componentDidMount() {

    //     const token = localStorage.getItem('token');

    //     const options = {
    //         headers: {
    //             'x-access-token': token,
    //         },
    //     };

    //     axios.get('https://nodejs-mysql-it-academy.herokuapp.com/my-hotels', options).then((res) => {
    //         this.setState({
    //             dataFromApi: res.data,
    //             hotels: res.data
    //         });
    //     });
    //     this.props.getHotels();
    //     axios.get('https://nodejs-mysql-it-academy.herokuapp.com/hotels/recommended').then((res) => {
    //         this.setState({
    //             recommendedHotels: res.data
    //         })
    //     })
    // }

    componentDidUpdate(prevProps, prevState) {
        if (prevState.currency !== this.state.currency) {
            const dataCopy = JSON.parse(JSON.stringify(this.state.dataFromApi));
            const newHotels = dataCopy.map(hotel => {
                hotel.price = Math.ceil(hotel.price * rates[this.state.currency])
                return hotel;
            });
            this.setState({
                hotels: newHotels
            })
        }
    }

    render() {
        return (
            <div className="AppH">
                <Header
                    convertPrice={this.convertPrice}
                />
                <div className="MainSectionH">
                    <Sidebar
                        sidebar_data={this.state.recommendedHotels}
                    />
                    <MainContainer
                        data={this.state.hotels}
                        sort={this.state.sort}
                        symbol={this.state.symbol}
                    />
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    hotels: state.hotels
})

const mapDispatchToProps = (dispatch) => ({
    getHotels: () => dispatch(getHotels()),
})

export default connect(mapStateToProps, mapDispatchToProps)(HomeView);